body {
  background-image: url(/Weather-app/static/media/wallpaper.5f9f6a27.jpg);
  background-repeat: no-repeat;
  
  overflow: hidden;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.headerheader{
    padding:20px;
    text-align: center;
    font-weight: bold;
    background-color: rgba(0,0,0, 0.6);
    color: white;
    margin-left: 19.55%;
    margin-right: 19.55%;
    box-shadow: 0px -5px 5px 0px black;
}
.sidecolumn{
    float: left;
    width: 20%;
    padding: 10px;
}
.maincolumn{
    float:left;
    width: 60%;
    padding:10px;
    background-color: rgba(0,0,0, 0.6);
    color: white;
    box-shadow: 0px 5px 5px 0px black;
}
.weather-info{
    max-height: 550px;
    overflow: auto;
}
::-webkit-scrollbar-track {
    background: transparent; 
}
::-webkit-scrollbar {
    width: 10px;
}
::-webkit-scrollbar-thumb {
    background: white; 
}
.submitbtn{
    background-color: transparent;
    border-width: 2px;
    border-color: white;
    padding: 10px;
    text-align: center;
    font-family: cursive;
    font-size: 16px;
    border-radius: 12px;
    margin: 5px;
    width: 30%;
    -webkit-transition-duration: 0.4s;
    transition-duration: 0.4s;
    cursor: pointer;
    outline:none;
}
.submitbtn:hover{
    color: yellow;
}

input[type=text]{
    font-weight: bold;
    background-color: transparent;
    width: 30%;
    padding: 10px;
    margin: 10px;
    box-sizing: border-box;
    border:none;
    border-bottom: 2px solid white;
    outline:none;
}

input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
    -webkit-transition: background-color 5000s ease-in-out 0s;
    transition: background-color 5000s ease-in-out 0s;
    -webkit-text-fill-color: #fff !important;
}

.weather-titletext{
    padding: 10px;
    margin: 10px;
    font-family: cursive;
    font-weight: bold;

}

span{
    color: yellow;
}

.errormsg{
    color:yellow;
}
